body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #2A415E;
  display: flex;
  flex-direction: column;
  align-items: center;
}


.greetings{
    font-size: 35px;
    color: white;
}
.TodoCounter{
    color: white;
    text-align: center;
    font-size: 20px;
}
.todoSearch{
    text-align: center;
    height: 25px;
    width: 100%;
    border: none;
    border-radius: 5px;
    background-color: #ABC3E3;
    color: white;
}
.TodoList{
    color: white;
    margin: 5px 0;

    /* display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    grid-gap: 20px 20px; */
}

.TodoItem:hover{
    cursor: pointer;
}
.TodoItem {
  position: relative;
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  margin: 20px 0;
  padding: 15px;
  border: solid 1px #4D76AB;
  border-radius: 10px;
}
 
.TodoItem-p {
  width: calc(100% - 120px);
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
}
.TodoItem-p--complete {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
  /* background-color: #CCC; */
}

.Icon-check {
  position: absolute;
  left: 12px;
}
.Icon-check:hover{
  
  -webkit-animation: myAnimIconCheck 2s ease 0s infinite alternate both;
  
          animation: myAnimIconCheck 2s ease 0s infinite alternate both;
}
.Icon-check--active {
  color: #4caf50;
}

.Icon-delete {
  position: absolute;
  right: 0;
}
.Icon-delete:hover {
  color: rgb(253, 68, 68);
  -webkit-animation: myAnimIconDelete 2s ease 0s infinite normal both;
          animation: myAnimIconDelete 2s ease 0s infinite normal both;
}

@-webkit-keyframes myAnimIconCheck {
	0%,
	100% {
		-webkit-transform: translateX(0%);
		        transform: translateX(0%);
		-webkit-transform-origin: 50% 50%;
		        transform-origin: 50% 50%;
	}

	15% {
		-webkit-transform: translateY(-7px) rotate(-4deg);
		        transform: translateY(-7px) rotate(-4deg);
	}

	45% {
		-webkit-transform: translateY(-5px) rotate(-3.6deg);
		        transform: translateY(-5px) rotate(-3.6deg);
	}

	75% {
		-webkit-transform: translateY(-3px) rotate(-1.2deg);
		        transform: translateY(-3px) rotate(-1.2deg);
	}
}

@keyframes myAnimIconCheck {
	0%,
	100% {
		-webkit-transform: translateX(0%);
		        transform: translateX(0%);
		-webkit-transform-origin: 50% 50%;
		        transform-origin: 50% 50%;
	}

	15% {
		-webkit-transform: translateY(-7px) rotate(-4deg);
		        transform: translateY(-7px) rotate(-4deg);
	}

	45% {
		-webkit-transform: translateY(-5px) rotate(-3.6deg);
		        transform: translateY(-5px) rotate(-3.6deg);
	}

	75% {
		-webkit-transform: translateY(-3px) rotate(-1.2deg);
		        transform: translateY(-3px) rotate(-1.2deg);
	}
}

@-webkit-keyframes myAnimIconDelete {
	0%,
	100% {
		-webkit-transform: translateX(0%);
		        transform: translateX(0%);
		-webkit-transform-origin: 50% 50%;
		        transform-origin: 50% 50%;
	}

	15% {
		-webkit-transform:  rotate(90deg);
		        transform:  rotate(90deg);
	}

	45% {
		-webkit-transform:  rotate(-90.6deg);
		        transform:  rotate(-90.6deg);
	}

	75% {
		-webkit-transform:  rotate(-30deg);
		        transform:  rotate(-30deg);
	}
}

@keyframes myAnimIconDelete {
	0%,
	100% {
		-webkit-transform: translateX(0%);
		        transform: translateX(0%);
		-webkit-transform-origin: 50% 50%;
		        transform-origin: 50% 50%;
	}

	15% {
		-webkit-transform:  rotate(90deg);
		        transform:  rotate(90deg);
	}

	45% {
		-webkit-transform:  rotate(-90.6deg);
		        transform:  rotate(-90.6deg);
	}

	75% {
		-webkit-transform:  rotate(-30deg);
		        transform:  rotate(-30deg);
	}
}
.modalform{
    margin: 100px 0 0 0;
    height: 150px;
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    box-shadow: 3px 3px 0.4em #8fc5ff86, -3px -3px 0.4em #8fc5ff86;
    background-color: #2A415E;
}
.modalform__input{
    width: 240px;
    height: 50px;
    margin: 0 auto; 
    border-radius: 5px;
}
.modalform__buttons > button{
    background-color: #6499DE;
    color: white;
    border: none;
    width: 90px;
    height: 25px;
    margin: 10px;
    border-radius: 5px;
}
.CreatetodoButton{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: none;
    color: white;
    background-color: #6499DE;

    position: fixed;
    -webkit-transform: rotate(0);
            transform: rotate(0);
    transition: .3s ease;
    z-index: 1;
}
.CreatetodoButton:hover{
    cursor: pointer;
    -webkit-transform: rotate(230deg);
            transform: rotate(230deg);
    background-color: #62C8F5;
}



.LoadingSkeleton{
    position: relative;
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  margin: 20px 0;
  padding: 15px;
  border: solid 1px #4D76AB;
  border-radius: 10px;

  -webkit-animation: myAnim 2s ease 0s infinite normal forwards;

          animation: myAnim 2s ease 0s infinite normal forwards;
}

@-webkit-keyframes myAnim {
	0% {
		opacity: .5;
	}

	50% {
		opacity: 0.2;
	}

	100% {
		opacity: .5;
	}
}

@keyframes myAnim {
	0% {
		opacity: .5;
	}

	50% {
		opacity: 0.2;
	}

	100% {
		opacity: .5;
	}
}
.CreateLastTodo>p{
    text-align: center;
    font-size: 18px;
    font-weight: 600;
    -webkit-animation: myAnimCreateLastTodo 2s ease 0s infinite alternate both;
            animation: myAnimCreateLastTodo 2s ease 0s infinite alternate both;
}
.CreateLastTodo>img{
    width: 300px;
}

@-webkit-keyframes myAnimCreateLastTodo {
	0%,
	100% {
		-webkit-transform: translateX(0%);
		        transform: translateX(0%);
		-webkit-transform-origin: 50% 50%;
		        transform-origin: 50% 50%;
	}

	15% {
		-webkit-transform: translateX(-15px) rotate(-4deg);
		        transform: translateX(-15px) rotate(-4deg);
	}

	30% {
		-webkit-transform: translateX(10px) rotate(4deg);
		        transform: translateX(10px) rotate(4deg);
	}

	45% {
		-webkit-transform: translateX(-10px) rotate(-3.6deg);
		        transform: translateX(-10px) rotate(-3.6deg);
	}

	60% {
		-webkit-transform: translateX(9px) rotate(2.4deg);
		        transform: translateX(9px) rotate(2.4deg);
	}

	75% {
		-webkit-transform: translateX(-6px) rotate(-1.2deg);
		        transform: translateX(-6px) rotate(-1.2deg);
	}
}

@keyframes myAnimCreateLastTodo {
	0%,
	100% {
		-webkit-transform: translateX(0%);
		        transform: translateX(0%);
		-webkit-transform-origin: 50% 50%;
		        transform-origin: 50% 50%;
	}

	15% {
		-webkit-transform: translateX(-15px) rotate(-4deg);
		        transform: translateX(-15px) rotate(-4deg);
	}

	30% {
		-webkit-transform: translateX(10px) rotate(4deg);
		        transform: translateX(10px) rotate(4deg);
	}

	45% {
		-webkit-transform: translateX(-10px) rotate(-3.6deg);
		        transform: translateX(-10px) rotate(-3.6deg);
	}

	60% {
		-webkit-transform: translateX(9px) rotate(2.4deg);
		        transform: translateX(9px) rotate(2.4deg);
	}

	75% {
		-webkit-transform: translateX(-6px) rotate(-1.2deg);
		        transform: translateX(-6px) rotate(-1.2deg);
	}
}
.FooterText{
    position: absolute;
    bottom: 30px;
    font-size: 18px;
    color: white;
    text-align: center;
}
.FooterText > a{
    text-decoration: none;
    color: #6499DE;
}
.FooterText > a:hover{
    color: #62C8F5;
}
.ModalBackground{
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    color: white;
}
